<template>
    <div class="login">
        <van-nav-bar title="会员登录" left-arrow @click-left="$router.go(-1)" />
        <div class="container">
            <div class="title">
                <!-- <h3>手机号登录</h3> -->
                <p>未注册的手机号登录后将自动注册</p>
            </div>

            <div class="form">
                <div class="form-item">
                    <input class="inp" v-model="phoneNumber" maxlength="11" placeholder="请输入手机号码" type="text">
                </div>
                <div class="form-item">
                    <input v-model="picCode" class="inp" maxlength="5" placeholder="请输入图形验证码" type="text">
                    <img v-show="picUrl" @click="getPicCode()" :src="picUrl" alt="">
                </div>
                <div class="form-item">
                    <input v-model="SMScode" class="inp" placeholder="请输入短信验证码" type="text">
                    <button ref="btnCode" @click="getCode()">{{ times === setTime?'获取验证码': times+'秒后重新发送'  }}</button>
                </div>
            </div>

            <div class="login-btn" @click="loginFn()">登录</div>
        </div>
    </div>
</template>

<script>
// import request from '@/utils/request'
// 导入api接口封装的函数
import { getReqPic, getReqCode, isLogin } from '@/api/login'

// 导入toast轻量级提示框
import { Toast } from 'vant'

export default {
  name: 'LoginPage',
  data () {
    return {
      setTime: 50,
      times: 50,
      timerId: null,
      phoneNumber: '',
      SMScode: '',
      picCode: '', // 用户输入的图形验证码
      picKey: '', // 验证码的唯一标识
      picUrl: '' // 验证码地址
    }
  },
  methods: {
    async getPicCode () {
    //   const { data: { base64, key } } = request.get('/captcha/image')
    // 导入封装好的api接口
      const { data: { base64, key } } = await getReqPic()
      this.picUrl = base64 // 存储地址
      this.picKey = key // 存储唯一标识
    },
    // 获取手机验证码前验证手机号与图形验证
    codeFn () {
      const reg = /^1\d{10}$/g
      const regs = /^[a-zA-Z0-9]{4}$/g
      //   const regss = /^[0-9]{6}$/g
      if (!reg.test(this.phoneNumber)) {
        Toast.fail('请输入11位的手机号')
        return false
      } else if (!regs.test(this.picCode)) {
        Toast.fail('请输入正确的图形验证码')
        return false
      }
      return true
    },

    // 获取短信验证码
    async getCode () {
      // 获取之前进行校验手机号与图形验证
      if (!this.codeFn()) {
        return
      }
      if (!this.timerId && this.setTime === this.times) {
        // 发送请求短信接口
        const res = await getReqCode(this.picCode, this.picKey, this.phoneNumber)
        // 后端判断图形验证码是否正确 用响应拦截器统一处理
        if (res.status !== 200) {
          Toast.fail(res.message)
          await this.$sleep(500)
          this.getPicCode()
          return
        }
        Toast.success('验证码发送成功：246810')
        // 禁用此按钮
        this.$refs.btnCode.disabled = true
        this.timerId = setInterval(() => {
          this.times--
          // console.log('正在倒计时')
          if (this.times <= 0) {
            clearInterval(this.timerId)
            this.timerId = null // 重置定时器
            this.times = this.setTime // 归位
            this.$refs.btnCode.disabled = false
          }
        }, 1000)
      } else {
        Toast.fail('请输入11位的手机号')
      }
    },
    // 登录验证
    async loginFn () {
      // 获取之前进行校验手机号与图形验证格式
      if (!this.codeFn()) {
        return
      }
      const regss = /^[0-9]{6}$/g
      if (!regss.test(this.SMScode)) {
        Toast.fail('请输入正确格式的短信验证码')
        return
      }
      Toast.success('登录成功')
      await this.$sleep(1000)
      const res = await isLogin(this.phoneNumber, this.SMScode)
      // 将token和userId存储到vuex中
      this.$store.commit('user/getUserInfo', res.data)

      // 判断跳转时是否有需要返回的路径
      const Url = this.$route.query.backUrl || '/'
      this.$router.replace(Url)
    }
  },
  created () {
    this.getPicCode()// 打开页面默认获取图形验证码
  },
  mounted () { },
  update () {
  },
  destroyed () {
    clearInterval(this.timerId)
  }
}
</script>

<style lang="less" scoped>
.container {
    padding: 49px 29px;
    text-align: center;
    .title {
        margin-bottom: 20px;

        h3 {
            font-size: 26px;
            font-weight: normal;
        }

        p {
            line-height: 40px;
            font-size: 14px;
            color: #b8b8b8;
        }
    }

    .form-item {
        border-bottom: 1px solid #f3f1f2;
        padding: 8px;
        margin-bottom: 14px;
        display: flex;
        align-items: center;

        .inp {
            display: block;
            border: none;
            outline: none;
            height: 32px;
            font-size: 14px;
            flex: 1;
        }

        img {
            width: 94px;
            height: 31px;
        }

        button {
            height: 31px;
            border: none;
            font-size: 13px;
            color: #cea26a;
            background-color: transparent;
            padding-right: 9px;
        }
    }

    .login-btn {
        width: 100%;
        height: 42px;
        margin-top: 39px;
        background: linear-gradient(90deg, #ecb53c, #ff9211);
        color: #fff;
        border-radius: 39px;
        box-shadow: 0 10px 20px 0 rgba(0, 0, 0, .1);
        letter-spacing: 2px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}</style>
